import './App.scss';
import {Routes, Route} from 'react-router-dom';
import Login from './Pages/Login';
import Aside from './Components/Aside/Aside';
import Home from './Pages/Home';
import Profile from './Pages/Profile';

//Images
import TwitterLogo from './Images/twitterlogo.svg'
import dots from './Images/dots.svg'
import customize from './Images/Customize.svg'
import image from './Images/image.svg'
import gif from './Images/gif.svg'
import stats from './Images/stats.svg'
import smile from './Images/smile.svg'
import time from './Images/time.svg'
import comments from './Images/comment.svg'
import retweet from './Images/retweet.svg'
import like from './Images/like.svg'
import share from './Images/share.svg'
import statistics from './Images/statistics.svg'
import search from './Images/search.svg'
import setting from './Images/settings.svg'
import arrow from './Images/arrow.svg'
import location from './Images/location.svg'
import frame from './Images/Frame.svg'
import balloon from './Images/balloon.svg'
import calendar from './Images/calendar.svg'
import pin from './Images/pin.svg'



function App() {
  return (
    <>
   <div className="container">
   <Aside logo={TwitterLogo} dots={dots} />
    <Routes>
    <Route path="/" element={<Home customize={customize} image={image} gif={gif} stats={stats} smile={smile} time={time} dots={dots} comments={comments} retweet={retweet} like={like} share={share} statistics={statistics} search={search} setting={setting} />}/>
    <Route path="/profile" element={<Profile arrow={arrow} location={location} frame={frame} balloon={balloon} calendar={calendar} pin={pin} dots={dots} comments={comments} retweet={retweet} like={like} share={share} statistics={statistics} search={search} setting={setting} />}/>
    </Routes>
   </div>
    <Routes>
    <Route path="/login" element={<Login logo={TwitterLogo}/>}/>
    </Routes>
    
    </>
    );
  }
  
  export default App;
  