function Login({ logo }) {


    return (
        <div className="login_box">
            <img src={logo} style={{ width: 50, height: 41 }} alt="" />
            <h2 className="login_title">Log in to Twitter</h2>
            <form className="login_form">
                <input className="login_input" type="text" placeholder="Phone number, email address" />
                <input className="login_input" type="password" placeholder="Password" />
                <button className="login_btn">Log In</button>
            </form>
        </div>
    )
}

export default Login