import { NavLink } from "react-router-dom"
import './Aside.scss'
import bobur from '../../Images/bobur.png'



function Aside({ logo, dots }) {
    return (
        <section className={`left_section`}>
            <div className="left-box">
                <nav className="nav">
                    <img src={logo} style={{ width: 40, height: 33, marginBottom: 49 }} alt="" />
                    <NavLink className="nav_links home" to="/">Home</NavLink>
                    <NavLink className="nav_links explore" to="/explore">Explore</NavLink>
                    <NavLink className="nav_links notification" to="/notifics">Notifications</NavLink>
                    <NavLink className="nav_links messages" to="/messages">Messages</NavLink>
                    <NavLink className="nav_links bookmarks" to="/bookmarks">Bookmarks</NavLink>
                    <NavLink className="nav_links lists" to="/lists">Lists</NavLink>
                    <NavLink className="nav_links profile" to="/profile">Profile</NavLink>
                    <NavLink className="nav_links more" to="/more">More</NavLink>

                    <button className="tweet_btn">Tweet</button>
                </nav>
                <div className="infor_box">
                    <img src={bobur} style={{ width: 50, height: 50 }} alt="bobur" />
                    <div className="connect">
                        <p className="name">Bobur</p>
                        <a className="link text16" href="@bobur_mavlonov">@bobur_mavlonov</a>
                    </div>
                    <img src={dots} style={{ width: 17, height: 4, marginLeft: 47 }} alt="" />
                </div>
            </div>
            <hr className="line" />
        </section>
    )
}

export default Aside